<template>
    <div>
        <van-nav-bar
                left-text="返回"
                left-arrow
                @click-left="onClickLeft"
        />
        <van-form @submit="onSubmit">
        <van-cell-group>
            <van-field
                    v-model="username"
                    name="用户名"
                    label="用户名"
                    placeholder="用户名"
                    :rules="[{ required: true, message: '请填写用户名' }]"
            />
            <van-field
                    v-model="email"
                    name="邮箱"
                    label="邮箱"
                    placeholder="邮箱"
                    :rules="[{pattern:yz_email, required: true, message:'请填写邮箱'}]"
            />
            <van-field
                    v-model="sms"
                    center
                    clearable
                    label="邮箱验证码"
                    placeholder="请输入邮箱验证码"
            >
                <template #button>
                    <van-button size="small"
                                type="primary"
                                @click="Send"
                                native-type="button"
                                :disabled="able">{{tip}}{{time}}</van-button>
                </template>
            </van-field>
            <van-field
                    v-model="password"
                    type="password"
                    name="密码"
                    label="密码"
                    placeholder="密码"
                    :rules="[{required: true, message:'请输入密码'}]"
            />
            <van-field
                    v-model="sure_password"
                    type="password"
                    name="确认密码"
                    label="确认密码"
                    placeholder="密码"
                    :rules="[{ required: true, message:'请填写密码'}]"
            />
            <div style="margin: 16px;">
                <van-button round block type="info" native-type="submit">
                    提交
                </van-button>
            </div>
        </van-cell-group>
        </van-form>
    </div>
</template>

<script>
    export default {
        name: "Register",
        data(){
            return{
                username:'',
                email:'',
                sms:'',
                password:'',
                sure_password:'',
                tip:'发送验证码',
                time:'',
                able:false,
                id:'',
                yz_email:'',
            }
        },
        methods:{
            onClickLeft() {
                this.$router.push({path:'/person_file'})
            },
            //发送验证码
            Send(){
                let params={
                    'key':'vv9544',
                    'email':this.email
                }
                this.$axios.post('https://zhuzuoji.com:8443//send_email',this.$qs.stringify(params))
                    .then(res=>{
                        // console.log(res)
                        if(res.data.msg=='已发送'&&res.status==200){
                            this.$toast('发送成功')
                            this.id=res.data.task_id
                            //倒计时
                            this.tip=''
                            this.time=30
                            let timer=setInterval(()=>{
                                this.time--
                                if(this.time==0){
                                    clearInterval(timer)
                                    this.tip='发送验证码'
                                    this.time=''
                                    this.able=false
                                }else{
                                    this.able=true
                                }

                            },1000)
                        }else {
                            this.$toast.fail('请联系管理员')
                        }
                    })
                    .catch(err=>{
                        if(err.toString().indexOf('404')){
                            this.$toast.loading({
                                message:'网络错误,请稍后'
                            })
                        }
                    })
            },
            //提交按钮
            onSubmit(){
                const user_password=this.password
                const user_surepassword=this.sure_password
                if(user_password!=user_surepassword){
                    this.$toast('两次输入密码不一致')
                }
                const params={
                    'key':'vv9544',
                    'email':this.email,
                    'task_id':this.id,
                    'verify_code':this.sms,
                    'password':this.password,
                    'password_confirm':this.sure_password,
                    'name':this.username
                }
                this.$axios.post('https://zhuzuoji.com/myapi/employee/register',this.$qs.stringify(params))
                    .then(res=>{
                        console.log(res)
                        const msg=res.data.msg
                        this.$toast.fail(msg)
                        this.$toast.success(msg)
                        if(res.status==200&&res.data.status){
                            this.$setCookie('email',this.email,1)
                            this.$router.push({
                                path:'/login',
                            })
                        }
                    })
                    .catch(err=>{
                        let error=err.toString()
                        if(error.indexOf('Network')!=-1){
                            this.$toast.fail('请检查网络')
                        }
                    })
            },
        }
    }
</script>

